import { createSlice, isAnyOf } from '@reduxjs/toolkit';

import {
  loginThunk,
  logoutThunk,
  refreshThunk,
  signupThunk,
  updAvatarThunk,
  updUserInfoThunk,
} from './thunk';

const initialState = {
  token: null,
  user: {
    id: null,
    email: null,
    name: null,
    avatar: null,
  },
  authenticated: false,
  isloading: false,
  error: null,
};

const handlePending = state => {
  state.isloading = true;
  state.error = null;
};
const handleFulfilled = (state, { payload }) => {
  state.isloading = false;
  state.token = payload.token;
  state.authenticated = true;
  state.user = payload.user;
};
const handleError = (state, { payload }) => {
  state.isloading = false;
  state.error = payload;
};
const handlerefreshFulfilled = (state, { payload }) => {
  state.isloading = false;
  state.authenticated = true;
  state.user = payload;
};
// const handleUpdAvaFulfilled = (state, { payload }) => {
//   // console.log('payload:', payload);
//   state.isloading = false;
//   state.authenticated = true;
//   state.user = payload;
// };

const authSlice = createSlice({
  name: 'auth',
  initialState: initialState,
  extraReducers: builder =>
    builder
      .addCase(signupThunk.fulfilled, handleFulfilled)

      .addCase(loginThunk.fulfilled, handleFulfilled)

      .addCase(refreshThunk.fulfilled, handlerefreshFulfilled)

      .addCase(updUserInfoThunk.fulfilled, handlerefreshFulfilled)

      .addCase(updAvatarThunk.fulfilled, handlerefreshFulfilled)

      .addCase(logoutThunk.fulfilled, () => initialState)

      .addMatcher(
        isAnyOf(
          logoutThunk.pending,
          signupThunk.pending,
          loginThunk.pending,
          refreshThunk.pending,
          updUserInfoThunk.pending,
          updAvatarThunk.pending
        ),
        handlePending
      )
      .addMatcher(
        isAnyOf(
          logoutThunk.rejected,
          signupThunk.rejected,
          loginThunk.rejected,
          refreshThunk.rejected,
          updUserInfoThunk.rejected,
          updAvatarThunk.rejected
        ),
        handleError
      ),
});

export const authReducer = authSlice.reducer;
